import { motion } from "framer-motion";

<motion.div
  initial={{ opacity: 0, y: -20 }}
  animate={{ opacity: 1, y: 0 }}
  transition={{ duration: 0.5 }}
  whileHover={pm25 > 100 ? { x: [-2, 2, -2, 2, 0] } : {}}
>
  <Paper elevation={3} sx={{ p: 2, borderRadius: 2, bgcolor: alertColor, color: "white", textAlign: "center" }}>
    <Typography variant="h5">Air Quality Alert</Typography>
    <Typography variant="h6">{pm25 > 100 ? "⚠️ Unhealthy Air!" : "✔️ Safe Air"}</Typography>
  </Paper>
</motion.div>
